                </div>

                <div class="col-sm-12 col-md-5 col-lg-5 col-xl-5 responsivefix991 rgt">
                    <div class="w-70 float-right p-0">
                        <div class="w-100 mt-0">
                            <div class="float-right p-0">
                                <div class="background-0 text-left form-check-inline mr-0">
                                    <a id="anctimersearch" href="javascript:;" class="btn btn-success mr-1 px-4" data-original-title="Search" @click="getListByDateType(dateType, selectedName)"><i class="fa fa-search"></i></a>
                                    <a href="javascript:;" title="" id="anccleartimersearch" class="btn btn-danger texttimerrest px-4" data-toggle-tooltip="tooltip" data-original-title="Clear all " @click="clearAllFilters(dateType, selectedName)"><i class="fa fa-refresh"></i></a>
                                </div>
                            </div>
                            <div class="col-7 float-right pl-0">
                                <div class="form-group mb-0">
                                    <input id="texttimersearch" maxlength="50" class="form-control" type="text" placeholder="Search By Project/Task/Description" autocomplete="off" v-model="search">
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    </div>

    <div class="col-md-12 p-0 mt-4 float-left border">
        <div class="row tt_status">

            <div class="col tt_bod-right">
                <em class="tt_icons ico_totalhrs float-left"></em>
                <span class="grid">
                    <span class="w-100 text-small mt-3">{{ $t('Total') }}</span>
                    <span class="w-100 text-large">{{ store.getActivityList.totalHours }}</span>
                </span>
            </div>

            <div class="col tt_bod-right">
                <em class="tt_icons ico_totalpaidtime float-left"></em>
                <span class="grid">
                    <span class="w-100 text-small mt-3">{{ $t('TotalPaidTime') }}</span>
                    <span class="w-100 text-large">{{ store.getActivityList.paidHours }}</span>
                </span>
            </div>

            <div class="col tt_bod-right" v-if="store.getTimer.enableBreak == 'yes'">
                <em class="tt_icons ico_totalbreaktime float-left"></em>
                <span class="grid">
                    <span class="w-100 text-small mt-3">{{ $t('TotalBreakTime') }}</span>
                    <span class="w-100 text-large">{{ store.getActivityList.totalBreakHours }}</span>
                </span>
            </div>

            <div class="col tt_bod-right" v-if="store.getTimer.enableBreak == 'yes'">
                <em class="tt_icons ico_paidbreaktime float-left"></em>
                <span class="grid">
                    <span class="w-100 text-small mt-3">{{ $t('PaidBreakTime') }}</span>
                    <span class="w-100 text-large">{{ store.getActivityList.paidBreakHours ==
                            null ? '00 H 00 M' : store.getActivityList.paidBreakHours }}</span>
                </span>
            </div>
            <div class="col tt_bod-right" v-if="store.getTimer.enableBreak != 'yes'">
                <em class="tt_icons ico_totalpaidtime float-left"></em>
                <span class="grid">
                    <span class="w-100 text-small mt-3">{{ $t('PausedTime') }}</span>
                    <span class="w-100 text-large">{{store.getActivityList.pausedHours }}</span>
                </span>
            </div>
            <div class="col tt_bod-right" v-if="store.getTimer.enableBreak == 'yes'">
                <em class="tt_icons ico_paidbreaktime disable float-left"></em>
                <span class="grid">
                    <!-- <span class="w-100 text-small mt-3">{{ $t('PaidBreakTime')}}</span> -->
                    <span class="w-100 text-small mt-3">{{ $t('UnPaidBreakTime') }}</span>
                    <span class="w-100 text-large">{{ store.getActivityList.unPaidBreakHours == null ? '00 H 00 M' :
                            store.getActivityList.unPaidBreakHours }}</span>
                </span>
            </div>
        </div>

    </div>
    <Alert :type="store.getAlertMessage.type" :message="store.getAlertMessage.message" v-bind:open="store.getAlertMessage.open" v-on:close="store.$state.getMessageAlert.open = false" />
    <!-- <div><span class="ml-3">{{ dateType }}</span></div>  -->
    <BulkEdit v-if="bulkEdit" v-on:close="openmodal(false)" />

